.gallery .gallery-row + .gallery-row {
  margin-top: 30px; }

.gallery .gallery-row > div[class^="col-"] {
  padding-bottom: 15px;
  padding-top: 15px; }

.gallery .thumb-wrapper .thumbnail, .gallery .thumb-wrapper .wp-caption {
  padding: 0;
  border: 0 none;
  border-radius: 0;
  margin-bottom: 15px; }

.gallery .thumb-wrapper .thumb-title {
  font-size: 18px; }

.content img,
.content iframe {
  max-width: 100%; }

.content iframe {
  width: 400px;
  height: 300px;
  margin: 0 0 15px; }

.comment-list {
  padding-left: 0;
  list-style: none; }

.comment-list ol {
  list-style: none; }

.gform_wrapper form {
  margin-top: 2em;
  margin-bottom: 1em; }

.gform_wrapper .gfield_required {
  padding-left: 1px;
  color: #0067AC; }

.gform_footer {
  margin-top: 15px; }

.ginput_container textarea {
  resize: vertical;
  height: auto; }

.gform_wrapper .gfield_error .gfield_label {
  color: #a94442; }

.gform_wrapper .gfield_error input,
.gform_wrapper .gfield_error select,
.gform_wrapper .gfield_error textarea {
  border-color: #ebccd1;
  background-color: #f2dede;
  color: #a94442; }
  .gform_wrapper .gfield_error input:focus,
  .gform_wrapper .gfield_error select:focus,
  .gform_wrapper .gfield_error textarea:focus {
    border-color: #a94442;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(169, 68, 66, 0.6);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(169, 68, 66, 0.6); }

.validation_message {
  display: none; }

.main {
  position: relative;
  min-height: 1px;
  padding-left: 10px;
  padding-right: 10px; }
  @media (min-width: 768px) {
    .main {
      float: left;
