.carousel.carousel-fade .item {
  opacity: 0;
  -webkit-transition-property: opacity;
  transition-property: opacity; }
  .carousel.carousel-fade .item.active {
    opacity: 1; }
    .carousel.carousel-fade .item.active.left, .carousel.carousel-fade .item.active.right {
      left: 0;
      z-index: 1;
      opacity: 0; }

.carousel.carousel-fade .next.left,
.carousel.carousel-fade .prev.right {
  opacity: 1; }

.carousel.fullscreen {
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000; }
  .carousel.fullscreen .carousel-indicators {
    bottom: 15%; }
  .carousel.fullscreen .carousel-inner {
    height: 100%; }
  .carousel.fullscreen .item {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    background-position: 50% 50%;
    height: 100%;
    width: 100%;
    -webkit-transition-duration: 2s;
    -moz-transition-duration: 2s;
    transition-duration: 2s; }
    .carousel.fullscreen .item > img {
      display: none; }
  .carousel.fullscreen .carousel-caption {
    display: table;
    position: static;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    color: inherit;
    padding: 0 5%; }
    .carousel.fullscreen .carousel-caption > div {
      display: table-cell;
      vertical-align: middle; }
    .carousel.fullscreen .carousel-caption h3 {
      font-size: 36px;
      font-weight: 400;
      text-transform: uppercase;
      -webkit-animation-duration: 2s;
      -moz-animation-duration: 2s;
      animation-duration: 2s;
      -webkit-animation-delay: 0.5s;
      -moz-animation-delay: 0.5s;
      animation-delay: 0.5s; }
    .carousel.fullscreen .carousel-caption p {
      font-size: 20px;
      -webkit-animation-duration: 2s;
      -moz-animation-duration: 2s;
      animation-duration: 2s;
      -webkit-animation-delay: 1.5s;
      -moz-animation-delay: 1.5s;
      animation-delay: 1.5s; }

